*{
    margin: 0;
    padding: 0;
}
.box{
    margin: auto;
    text-align: center;
    background-color: #18181c;
    padding-bottom:50px;
}
.title{
    width: 100%;
    height: 15%;
    font-size: 2rem;
    background-color:#23212b;
    color: #3569e4;
    line-height: 155%;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}
.input-bar{
    margin: auto;
    width: 75%;
    height: 50px;
    margin-top: 5%;
    background-color: #23212b;
}
#inp{
    border: none;
    overflow: hidden;
    height: 50px;
    width: 90%;
    color: #fff;
    background-color: #23212b;
    float: left;
    font-size: 1.5rem;
}
#add{
    height: 50px;
    width: 10%;
    z-index: 100;
}
.input{
    line-height: 25px;
    color: #fff;
}
.list-bar{
    margin-bottom: 5%;
}
.now{
    margin: auto;
    width: 75%;
    height: 250px;
    margin-top: 5%;
    /* border-radius: 50px; */
    background-color: #23212b;
}
.ok{
    margin: auto;
    width: 75%;
    height: 250px;
    margin-top: 5%;
    /* border-radius: 50px; */
    background-color: #23212b;
}
ul{
    color: #fff;
    margin-top: 5%;
}
h2 {
    padding-top: 15px;
    color: #fff;
}
button{
    float: right;
    height: 20px;
    width: 40px;
    text-align: center;
    line-height: 20px;
    background-color: #3569e4;
    color: #fff;
}
li{
    border-left: #3569e4 2px solid;
    list-style: none;
    height: 20px;
}
.rooter{
    color: #fff;
    font-size: 0.9rem;
}

